@use 'variables' as v;
@use '../base/mixins' as m;

html {
    box-sizing: border-box;
    font-size: 62.5%;
}

*, 
*:before, 
*:after {
    box-sizing: inherit;
}

body {
     font-family: v.$fuentePrincipal;
     font-size: 1.6rem;
     line-height: 1.15;
}

span {
    text-transform: capitalize;
}

p {
    color: v.$negro;
}

a {
    text-decoration: none;
    background-color: transparent;
}

img {
    display: block;
    max-width: 100%;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3.6rem;
}

h3 {
    font-size: 3.2rem;
}

ul {
    padding: 0;
}

li {
    list-style: none;
}

[class*="contenedor"] {
    max-width: 1200px;
    width: 95%;
    margin: 0 auto;
}

.text-center {
    text-align: center;
}
