html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: sans-serif;
  font-size: 1.6rem;
  line-height: 1.15;
}

span {
  text-transform: capitalize;
}

p {
  color: #000;
}

a {
  text-decoration: none;
  background-color: transparent;
}

img {
  display: block;
  max-width: 100%;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

ul {
  padding: 0;
}

li {
  list-style: none;
}

[class*=contenedor] {
  max-width: 1200px;
  width: 95%;
  margin: 0 auto;
}

.text-center {
  text-align: center;
}

.avif .header {
  background-image: url("../img/header.avif");
}

.webp.notavif .header {
  background-image: url("../img/header.webp");
}

.notwebp.notavif .header {
  background-image: url("../img/header.jpg");
}

.header {
  padding: 3rem 0 20rem;
  font-family: sans-serif;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header {
    padding: 3rem 0 20rem;
  }
}
.header__menu {
  display: none;
}
@media (min-width: 768px) {
  .header__menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__menu p {
  color: #fff;
  font-size: 1.6rem;
  margin: 0;
}
.header__logo p {
  font-size: 2rem;
}
.header__anfitrion {
  text-transform: capitalize;
}

.menu ul {
  display: flex;
  gap: 4rem;
  margin: 0;
}
.menu ul p {
  font-size: 1.6rem;
}

.form {
  margin: 5rem 0 2rem;
  padding: 0;
}
.form__content {
  border: none;
  background-color: #fff;
  border-radius: 5rem;
  box-shadow: 0 3px 7px #686868;
  padding: 3rem 0;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 992px) {
  .form__content {
    display: flex;
    padding: 0;
    justify-content: space-between;
    max-width: 90rem;
    width: 100%;
    margin: 0 auto;
  }
}
.form__campo {
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
  transition: all 0.1s;
}
@media (min-width: 768px) {
  .form__campo:first-of-type {
    padding-left: 4rem;
  }
}
.form__campo:hover {
  background-color: #f3f3f3;
  border-radius: 5rem;
  box-shadow: 0 3px 7px #686868;
}
.form__label {
  font-size: 1.2rem;
  font-weight: 900;
}
.form input[type=text],
.form input[type=number],
.form input[type=date] {
  color: #767676;
  border: none;
  width: 100%;
}
.form__btn {
  background-image: radial-gradient(circle, #ff385c, #bd1e59);
  height: 4rem;
  color: #fff;
  border: none;
  border-radius: 2rem;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 1.2rem;
  padding: 0 2rem;
}

.busqueda {
  text-align: center;
  width: 100%;
  margin: 5rem 0 0;
}
@media (min-width: 992px) {
  .busqueda {
    margin: 30rem 0 0;
  }
}
.busqueda__title {
  font-size: 2rem;
  font-weight: 400;
  margin: 2rem 0;
}
.busqueda__btn {
  display: inline-block;
  color: #ff385c;
  background-color: #fff;
  text-transform: capitalize;
  padding: 2rem 4rem;
  border-radius: 5rem;
  font-weight: 700;
  font-size: 2rem;
  width: 100%;
  text-align: center;
}
@media (min-width: 768px) {
  .busqueda__btn {
    width: auto;
    margin-top: 1rem;
  }
}

.alojamientos {
  padding: 2rem 0;
}
.alojamientos__title {
  margin: 2rem 0;
  font-weight: 600;
  font-size: 3.2rem;
  line-height: 1.5;
}
.alojamientos__cards {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .alojamientos__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .alojamientos__cards {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.alojamientos__cards .card {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .alojamientos__cards .card {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2.5rem;
  }
}
@media (min-width: 480px) {
  .alojamientos__cards .card {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }
}
.alojamientos__cards .card__img {
  width: 14rem;
  margin: 0 auto;
}
.alojamientos__cards .card__title {
  line-height: 1.5;
  margin: 0;
  font-size: 2.4rem;
}
.alojamientos__cards .card__p {
  margin: 0;
  font-size: 2rem;
}

.lugar {
  padding: 2rem 0;
}
.lugar__title {
  margin: 2rem 0;
  font-weight: 600;
  font-size: 3.2rem;
  line-height: 1.5;
}
.lugar__cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 4rem;
  word-wrap: break-word;
  overflow-x: scroll;
}
@media (min-width: 768px) {
  .lugar__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    word-wrap: none;
  }
}
@media (min-width: 992px) {
  .lugar__cards {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
    word-wrap: none;
  }
}
.lugar__cards .card {
  width: 30rem;
}
@media (min-width: 768px) {
  .lugar__cards .card {
    width: auto;
  }
}
.lugar__cards .card__img {
  width: 100%;
  border-radius: 1rem;
}
.lugar__cards .card__p {
  font-size: 1.8rem;
}

.avif .anfitrion {
  background-image: url("../img/anfitrion.avif");
}
@media (min-width: 768px) {
  .avif .anfitrion {
    background-image: linear-gradient(to right, #000 0, #000 45%, transparent 55%), url("../img/anfitrion.avif");
  }
}

.webp.notavif .anfitrion {
  background-image: url("../img/anfitrion.webp");
}
@media (min-width: 768px) {
  .webp.notavif .anfitrion {
    background-image: linear-gradient(to right, #000 0, #000 45%, transparent 55%), url("../img/anfitrion.webp");
  }
}

.notwebp.notavif .anfitrion {
  background-image: url("../img/anfitrion.jpg");
}
@media (min-width: 768px) {
  .notwebp.notavif .anfitrion {
    background-image: linear-gradient(to right, #000 0, #000 45%, transparent 55%), url("../img/anfitrion.jpg");
  }
}

.anfitrion {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-row: 2/3;
  background-size: 110%;
  background-position: 25% 67%;
  background-repeat: no-repeat;
}
@media (min-width: 400px) {
  .anfitrion {
    background-position: 25% 76%;
  }
}
@media (min-width: 450px) {
  .anfitrion {
    background-position: 25% 85%;
  }
}
@media (min-width: 500px) {
  .anfitrion {
    background-position: 25% 115%;
  }
}
@media (min-width: 600px) {
  .anfitrion {
    background-position: 25% 157%;
  }
}
@media (min-width: 700px) {
  .anfitrion {
    background-position: 25% 240%;
  }
}
@media (min-width: 768px) {
  .anfitrion {
    grid-template-rows: unset;
    background-position: 100%, 0 10%;
    background-size: cover, 115% 100%;
    padding: 15rem 5rem;
  }
}
@media (min-width: 840px) {
  .anfitrion {
    background-position: 100%, -100% 10%;
    background-size: cover, 105% 100%;
  }
}
@media (min-width: 928px) {
  .anfitrion {
    background-position: 100%, -150% 70%;
    background-size: cover, 105% 115%;
  }
}
@media (min-width: 992px) {
  .anfitrion {
    background-position: 100%, 50px 30%;
    background-size: cover, 100% auto;
  }
}
.anfitrion__info {
  grid-row: 1/2;
  background-color: #000;
  padding: 5rem;
}
@media (min-width: 768px) {
  .anfitrion__info {
    background-color: unset;
    grid-row: auto;
    padding: 0;
    max-width: 50%;
  }
}
.anfitrion__title {
  color: #fff;
  font-size: 3.2rem;
  margin: 2rem 0;
  line-height: 1.5;
}
.anfitrion__p {
  color: #fff;
  font-size: 2rem;
}
.anfitrion__btn {
  display: inline-block;
  color: #000;
  background-color: #f3f3f3;
  padding: 1rem 5rem;
  font-weight: 900;
  border-radius: 1rem;
  text-align: center;
  width: 100%;
  font-size: 2rem;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .anfitrion__btn {
    width: auto;
    padding: 1rem 4rem;
  }
}

.experiencias {
  padding: 2rem 0;
}
.experiencias__title {
  font-size: 3.2rem;
  text-transform: capitalize;
}
.experiencias__p {
  font-size: 2rem;
}
.experiencias__card {
  display: grid;
  grid-template-columns: repeat(3, 30rem);
  gap: 2rem;
  overflow-x: scroll;
}
@media (min-width: 768px) {
  .experiencias__card {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.experiencias__card .card__img {
  border-radius: 1rem;
}
.experiencias__card .card__title {
  font-size: 2rem;
}
.experiencias__card .card__p {
  font-size: 1.8rem;
}

.footer {
  background-color: #f3f3f3;
  padding: 5rem 0 0;
}
@media (min-width: 768px) {
  .footer .menu-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .footer .menu-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.footer .nav__title {
  font-size: 1.2rem;
  font-weight: 900;
  text-transform: uppercase;
  margin: 2rem 0;
}
.footer .nav__a {
  display: block;
  color: #686868;
  font-size: 1.4rem;
  margin-bottom: 1rem;
  text-transform: capitalize;
}
.footer .menu__horizontal {
  border-top: 1px solid #cecece;
}
.footer .menu__horizontal ul {
  padding: 2rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .footer .menu__horizontal ul {
    display: flex;
    flex-direction: row;
    gap: 2rem;
  }
}
.footer .menu__horizontal li {
  display: flex;
}
.footer .menu__horizontal li::before {
  display: block;
  content: " - ";
  margin-right: 0.5rem;
}